import style from './Skills.module.scss'
import {Skill} from "./Skill";
import react_logo from '../../Images/icons/Skills_logo/react.svg'
import html_logo from '../../Images/icons/Skills_logo/html5.svg'
import css_logo from '../../Images/icons/Skills_logo/css.svg'
import js_logo from '../../Images/icons/Skills_logo/js.svg'
import redux_logo from '../../Images/icons/Skills_logo/redux.svg'
import typescript_logo from '../../Images/icons/Skills_logo/_typescript.png'
import github_logo from '../../Images/icons/Skills_logo/github.gif'
import sass_logo from '../../Images/icons/Skills_logo/sass.svg'

export const Skills =()=> {


    return (
        <div className={style.skills_container}>

            <Skill title={'HTML'} logo={html_logo} info={'HTML        -  .'}/>
            <Skill title={'CSS'} logo={css_logo} info={'CSS       ,     .'}/>
            <Skill title={'JS'} logo={js_logo} info={'JavaScript    .'}/>
            <Skill title={'React'} logo={react_logo} info={'React  JavaScript-    .'}/>
            <Skill title={'Redux'} logo={redux_logo} info={'Redux    JavaScript     .'}/>
            <Skill title={'TypeScript'} logo={typescript_logo} info={'TypeScript     -,   JavaScript.'}/>
            <Skill title={'GitHub'} logo={github_logo} info={'GitHub   -   IT-    .'}/>
            <Skill title={'Sass'} logo={sass_logo} info={'Sass   ,      CSS-      .'}/>

        </div>
    )
}